.modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .modalContent {
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    max-width: 400px;
    width: 100%;
    color: black;
  }
  
  input {
    display: block;
    margin-bottom: 10px;
    padding: 8px;
    width: 100%;
    box-sizing: border-box;
  }
  .modalEnter {
    opacity: 0;
    transform: scale(0.9);
}
.modalEnterActive {
    opacity: 1;
    transform: scale(1);
    transition: opacity 300ms, transform 300ms;
}
.modalExit {
    opacity: 1;
    transform: scale(1);
}
.modalExitActive {
    opacity: 0;
    transform: scale(0.9);
    transition: opacity 300ms, transform 300ms;
}

  